.navbar {
    display: flex;
    justify-content: space-between;
    background-color: #222;
    padding: 1em;
}

.list{
    display: flex;
    list-style: none ;
    align-items: center;
}

.item{
    margin-right: 1em;
}

.item a{
    color: #fff;
    text-decoration: none;
    transition: 0.5s;
}
.item a:hover {
    color: #ba55d3;
    font-size: 1.5em;
}